.service{
  width:80%;
  margin: auto;
  height: 60vh;

  position: relative;
  overflow: hidden;
}

.service img{
  width:100%;
  height: 100%;
  transition: all .5s;
}

.box_1{
  width:20px;
  height: 20px;
  border-top:2px solid white;
  border-left: 2px solid white;
  z-index: 1000;
  position: absolute;
  top:10px;
  left:10px;
  opacity: 0;
  transition: all .5s;
}

.box_4{
  width:20px;
  height: 20px;
  border-bottom:2px solid white;
  border-right: 2px solid white;
  z-index: 1000;
  position: absolute;
  bottom:50px;
  right:10px;
  opacity: 0;
  transition: all .5s;
}

.service:hover .box_1{
  width: 120px;
  height: 120px;
  opacity: 1;
  transition: all .5s;
}
.service:hover{
  cursor: pointer;
}
.service:hover .box_4{
  width: 120px;
  height: 120px;
  opacity: 1;
  transition: all .5s;
}

.service:hover img {
  transform: scale(1.2);
  transform: translate3d(0,-40px,0);
  opacity: 0.5;
  transition: all .5s;
}

.fa-briefcase{
  color:yellow;
  margin: auto;
 
}

.service-icon{
  display: flex;
  justify-content: center;
  align-items: center;
}

.fa-briefcase::before{
  font-size: 40px;
}

.service-cont-box{
  width:100%;
  padding-top: 10px;
  height: 40px;
  position: absolute;
  transform: translate3d(0,40px,0);
  background-color: rgb(255,99,71);
  transition: all .5s;
}

.service:hover .service-cont-box{
  opacity: 1;
  transition: all .5s;
  transform: translate3d(0,-40px,0);
}

.service-cont{
  width: 100%;
  height: 100%;
