.nav-whole{
    width: 100vw;
    height: 90vh;
    display: grid;
    justify-content: center;
    align-items: center;
    transition: all 2s ease-in-out;

}

.animate-nav{
    width: 100vw;
    height: 90vh;
    display: grid;
    justify-content: center;
    align-items: center;
    
}



.hamburger-whole{
    width:60px;
    height:60px;
    position: absolute;
    right:17px;
    top:17px;
    display: grid;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 10000;
    border-radius: 50%;
    background-color: red;
  
}

.hamburger-whole .ln1{
   position: relative;
   top:10px;
    transition: 1s ease-in-out;
}
.hamburger-whole .ln2{
    transition: 1s ease-in-out;
}
.hamburger-whole .ln3{
    position: relative;
    bottom: 10px;
    transition: 1s ease-in-out;
}

.animate-ham{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: fixed;
    right:30px;
    top:30px;
    cursor: pointer;
    z-index: 10000;
}

.animate-ham .ln1{
    background: linear-gradient(45deg, #f3ec78, #af4261);
    transition:all 2s ease-in-out;
   transform: rotate(-45deg)
}
.animate-ham .ln3{
    background: linear-gradient(45deg, #f3ec78, #af4261);
   transform: rotate(45deg);
   position: relative;
   bottom: 16px;
   transition:all 2s ease-in-out;
}

.animate-ham .ln2{
opacity: 0;
}



.line{
    width:30px;
    margin:auto;
