}

.nav-cont-whole{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-clip-path: circle(2.5% at 96% -5%);
            clip-path: circle(2.5% at 96% -5%);
    background-color:red;
