}

.circle-nav{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-clip-path: circle(150% at 96% -5%);
            clip-path: circle(150% at 96% -5%);
    background-color:rgb(21,32,52);
    transition: all 2s;
    position: fixed;
}

